@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #2B2D42;
  display: flex;
  height: 90vh;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  position: relative;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.timebox{padding:8px}.timebox__heading{color:#edf2f4;text-align:center}.timebox__button{background:#2b2d42;border:none;border-radius:16px;color:#edf2f4;padding:6px 16px;border:1px solid #edf2f4;width:28%}.timebox__button-wrapper{margin:24px 0 24px;display:flex;justify-content:space-evenly}@media(min-width: 360px){.timebox__button-wrapper{margin-top:48px}}.timebox__pauses{display:block;text-align:center;color:#edf2f4}@media(min-width: 360px){.timebox__pauses{margin-top:32px}}.timebox__pauses>span{color:#d90429;font-weight:700}
.progress-bar{display:flex;height:16px;border:1px solid #2b2d42;padding:4px;border-radius:5px;background:#edf2f4}.progress-bar__inside{border-radius:3px;height:100%;background-color:#2b2d42}
.clock{display:flex;padding:20px;justify-content:center;align-items:center;height:120px;width:120px;color:#edf2f4;text-align:center;font-size:32px;background:#2b2d42;border-radius:50%;margin:0 auto 64px}
.timebox-editor{display:flex;flex-direction:column;align-items:center;padding:16px 8px 0}.timebox-editor__input{display:block;margin-top:8px;border-radius:12px;border:1px solid #2b2d42;background:#edf2f4;color:#2b2d42;padding:6px;outline:none}.timebox-editor__input:focus{border-color:#edf2f4;background-color:#2b2d42;color:#edf2f4}.timebox-editor__title{display:block;color:#2b2d42;font-weight:700;font-size:18px;margin-bottom:16px}.timebox-editor__button{display:block;background:#2b2d42;border:1px solid #edf2f4;border-radius:16px;color:#edf2f4;padding:6px 16px}
.history{margin-top:16px;display:block;width:90%}.history__heading{text-align:center;color:#2b2d42;margin-bottom:0;font-size:20px}@media(min-width: 360px){.history__heading{margin:24px 0}}@media(min-width: 400px){.history__heading{font-size:28px;margin:32px 0}}@media(min-height: 800px){.history__heading{margin-top:32px}}.history-list{padding:0 8px 0 0;list-style:none;color:#edf2f4;font-size:10px;line-height:2}@media(min-width: 360px){.history-list{font-size:12px}}@media(min-width: 400px){.history-list{line-height:2.5;font-size:13px}}@media(min-height: 800px){.history-list{line-height:3}}@media(min-width: 400px){.history-list__heading{font-size:16px}}.history-wrapper{display:flex;justify-content:space-around}.history__clear-button{display:block;margin:0 auto;background:#2b2d42;border:1px solid #edf2f4;border-radius:16px;color:#edf2f4;padding:6px 16px}.special{font-size:12px;text-align:center;color:#edf2f4;margin-top:32px}
.end-of-time{width:100%;height:100vh;position:absolute;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;background:#2b2d42;top:0;left:0;color:#fff;z-index:5}.info{display:flex;flex-direction:column;align-items:center;border:2px solid #edf2f4;border-radius:8px;padding:8px 16px;min-width:65%}.info__title{font-size:24px}.info__time{font-size:12px}.info__pauses{font-size:14px}.stop{display:flex;flex-direction:column;align-items:center}.stop__title{font-size:28px;color:#d90429}.stop__button{display:block;background:#ef233c;border:1px solid #edf2f4;border-radius:16px;color:#edf2f4;padding:6px 16px;width:150px;margin-top:16px}
.app{padding:0 0 16px;width:90vw;min-height:90vh;margin-top:32px;background:#8d99ae;border-radius:8px}@media(min-width: 768px){.app{width:450px;min-height:70vh}}@media(min-width: 1024px)and (min-height: 1366px){.app{min-height:50vh}}h1{margin-top:0;color:#2b2d42;text-align:center;padding-top:16px}@media(min-width: 360px){h1{font-size:36px}}@media(min-width: 400px){h1{font-size:40px}}
